* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@media (width <= 600px) {
  html {
    font-size: 70%;
  }
}

:root {
  --background_color: #1e1e1e;
  --text_color1: #acacac;
  --text_color_highlight1: #9872a2;
  --text_color_highlight2: #6089b4;
  --color1: #8080ff;
  --color2: #408080;
  --color3: #4545454f;
  --text_color1_translucent: #acacaca6;
}

main {
  flex-direction: column;
  align-items: center;
  display: flex;
}

h1 {
  color: var(--text_color_highlight1);
  padding: 10px 0;
  font-size: 3rem;
}

h2, h3 {
  color: var(--text_color_highlight2);
}

p, li {
  color: var(--text_color1);
  font-size: 1.2rem;
  line-height: 1.5em;
}

a {
  color: var(--color1);
}

a:hover {
  color: var(--text_color_highlight2);
}

strong {
  color: var(--color2);
}

html {
  background: var(--background_color);
}

.small {
  font-size: .7rem;
}

.section_title {
  padding-left: 10%;
}

.section_title h1 {
  text-align: left;
}

.section_title .line {
  width: 90%;
  background: linear-gradient(90deg, var(--text_color1_translucent), var(--background_color));
  height: 5px;
  border: none;
  margin-left: 0;
}

#navbar {
  height: 3rem;
  width: 100%;
  background-color: var(--color3);
}

.nav-links {
  justify-content: flex-end;
  align-items: stretch;
  display: flex;
}

.nav-links a {
  margin: 7.5px 20px;
  font-size: 1.5em;
  text-decoration: none;
}

#navbar.sticky {
  opacity: .95;
  position: fixed;
}

.section {
  height: calc(100vh - 1.5rem);
  width: 60vw;
}

#welcome-section {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

#welcome-section h1 {
  padding-bottom: 5px;
  font-size: 6rem;
}

#descriptors {
  opacity: .75;
}

#about-me {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.line {
  border: 2px solid var(--text_color1);
  width: 50%;
  margin: 10px auto;
}

#about-me p, li {
  color: var(--text_color1);
  font-weight: 500;
}

#about-me p {
  text-indent: -5%;
  margin: 5px 15%;
}

#about-me li {
  margin: 5px 3%;
  list-style-type: circle;
}

#skills {
  flex-wrap: wrap;
  margin-left: 14%;
  display: flex;
}

#projects {
  height: 200vh;
}

.projects_content {
  height: 95%;
  width: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.project-wrapper {
  width: 80%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.project_content_container {
  width: 75%;
  background-color: var(--color3);
  height: 12rem;
  border-radius: 10px;
}

.project_content_container h2 {
  text-align: center;
  margin: 10px auto;
  font-size: 2rem;
}

.project_content_container p {
  text-align: center;
  padding: 5px 5%;
}

.project-image {
  height: 15em;
  filter: grayscale(80%);
  border-radius: 5px;
  transition: all .2s ease-in-out;
}

.project-image:hover {
  filter: none;
}

.image-border {
  background: linear-gradient(270deg, #ff3f3f, 25%, #0e8bc5);
  border-radius: 5px;
  margin-left: -5%;
  margin-right: -10%;
  padding: 3px 3px 0;
  position: relative;
}

.two-image {
  margin-right: -20%;
}

#contacts {
  height: 50vh;
}

#contacts p {
  text-align: center;
  padding: 10px;
}

.social-links, .social-icons {
  width: 60%;
  justify-content: space-evenly;
  align-items: center;
  margin: 30px auto;
  font-size: 1.3rem;
  display: flex;
}

.social-icon {
  height: 50px;
}

